/*
 * JS 
 * type yyyy-mm-dd hh:ii:ss / mm-dd / hh:ii / yyyy-mm
 * date 1554954127000
 * timeFormat('yyyy-mm-dd hh:ii:ss',1554954127000)
 * 
 * var d = new Date('2010-5').getTime();
 * let month =timeFormat('yyyy-mm', d);//
 * console.log(month)
 * month=new Date(month).getTime();
 * console.log(timeFormat('yyyy-mm', month))
 *  Unix  1000 
 */
export function timeFormat(type, date) {
	var date = new Date(date);
	var o = {
		"m+": date.getMonth() + 1, //   
		"d+": date.getDate(), //   
		"h+": date.getHours(), //   
		"i+": date.getMinutes(), //   
		"s+": date.getSeconds(), //   
	};
	if (/(y+)/.test(type)) {
		type = type.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
	};
	for (var k in o) {
		if (new RegExp("(" + k + ")").test(type)) {
			type = type.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k])
				.length)));
		};
	}
	return type;
}
//js()
//1.
export function getDateBetween(start, end) {
	var result = [];
	//
	var startTime = new Date(start);
	var endTime = new Date(end);
	while (endTime - startTime >= 0) {
		let year = startTime.getFullYear();
		let month = startTime.getMonth();
		month = month < 9 ? '0' + (month + 1) : month + 1;
		let day = startTime.getDate().toString().length == 1 ? "0" + startTime.getDate() : startTime.getDate();
		//
		result.push(year + "-" + month + "-" + day);
		//
		startTime.setDate(startTime.getDate() + 1);
	}
	return result;
}
// 
export function getMonthBetween(start, end) {
	//
	var result = [];
	//
	var s = start.split("-");
	//
	var e = end.split("-");
	//
	var min = new Date();
	var max = new Date();
	//
	min.setFullYear(s[0], s[1]);
	//
	max.setFullYear(e[0], e[1]);

	//
	var curr = min;
	//
	var str = "";
	//
	while (curr <= max) {
		//
		var month = curr.getMonth();
		//012
		if (month === 0) {
			str = (curr.getFullYear() - 1) + "-" + 12;
		} else { //
			str = curr.getFullYear() + "-" + (month < 10 ? ("0" + month) : month);
		}
		//
		result.push(str);
		//
		curr.setMonth(month + 1);
	}
	return result;
}


//
export function getYearBetween(start, end) {
	var result = [];
	//
	var startTime = new Date(start);
	var endTime = new Date(end);
	while (endTime - startTime >= 0) {
		//
		let year = startTime.getFullYear();
		//
		result.push(year);
		//
		startTime.setFullYear(startTime.getFullYear() + 1);
	}
	return result;
}



//
export function getDaysInMonth(year, month) {
  const daysOfMonth = [];
  month = parseInt(month, 10);
  const lastDayOfMonth = new Date(year, month, 0).getDate();
  for (let i = 1; i <= lastDayOfMonth; i++) {
    if (i < 10) {
      daysOfMonth.push(year+"-"+month+"-"+"0" + i);
    } else {
      daysOfMonth.push(year+"-"+month+"-"+i);
    }
  }
  return daysOfMonth;
}