/**
 * @description: JS 
 * @param {String} typeyyyy-mm-dd hh:ii:ss / mm-dd / hh:ii / yyyy-mm// yyyy // mm // dd
 * @param {data} date 1554954127000
 * timeFormat('yyyy-mm-dd hh:ii:ss',1554954127000)
 * @return {any}
 */
export function timeFormat(type, date) {
  var date = new Date(date)
  var o = {
    'm+': date.getMonth() + 1, //
    'd+': date.getDate(), //
    'h+': date.getHours(), //
    'i+': date.getMinutes(), //
    's+': date.getSeconds(), //
  }
  if (/(y+)/.test(type)) {
    type = type.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(type)) {
      type = type.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length))
    }
  }
  return type
}
/**
 * @description: 
 * @param {String} year 2021
 * @param {String} month 05
 * @return {String[]}
 */
export function getDaysInMonth(year, month) {
  const daysOfMonth = []
  month = parseInt(month, 10)
  month = month >= 10 ? month : `0${month}`
  const lastDayOfMonth = new Date(year, month, 0).getDate()
  for (let i = 1; i <= lastDayOfMonth; i++) {
    if (i < 10) {
      daysOfMonth.push(year + '-' + month + '-' + '0' + i)
    } else {
      daysOfMonth.push(year + '-' + month + '-' + i)
    }
  }
  return daysOfMonth
}
/**
 * @description: 
 * @param {String} date
 * @return {*}
 */
export function getDaysInWeek(date) {
  let currentDate = new Date(date)
  let timesStamp = currentDate.getTime()
  let currenDay = currentDate.getDay()

  let dates = []

  for (var i = 0; i < 7; i++) {
    dates.push(new Date(timesStamp + 24 * 60 * 60 * 1000 * (i - ((currenDay + 6) % 7))).toLocaleDateString().replace(/\//g, '-'))
  }

  return dates
}
/**
 * @description: 
 * @param {String} start  2020-12
 * @param {String} end  2021-5
 * 
 * console.log(getMonthBetween('2020-12','2021-5'));
 * @return {String[]}
 */
export function getMonthBetween(start, end) {
  //
  var result = []
  //
  var s = start.split('-')
  //
  var e = end.split('-')
  //
  var min = new Date()
  var max = new Date()
  //
  min.setFullYear(s[0], s[1])
  //
  max.setFullYear(e[0], e[1])

  //
  var curr = min
  //
  var str = ''
  //
  while (curr <= max) {
    //
    var month = curr.getMonth()
    //012
    if (month === 0) {
      str = curr.getFullYear() - 1 + '-' + 12
    } else {
      //
      str = curr.getFullYear() + '-' + (month < 10 ? '0' + month : month)
    }
    //
    result.push(str)
    //
    curr.setMonth(month + 1)
  }
  return result
}

/**
 * @description:x
 * @param {*} date 
 * @param {*} monthNum 
 * getPreMonthDay(xxxxxx, 5) //5
 * @return {*}
 */
export function getPreMonthDay(date, monthNum) {
  var dateArr = date.split('-')
  var year = dateArr[0] //
  var month = dateArr[1] //
  var day = dateArr[2] //
  var days = new Date(year, month, 0)
  days = days.getDate() //
  var year2 = year
  var month2 = parseInt(month) - monthNum
  if (month2 <= 0) {
    var absM = Math.abs(month2)
    year2 = parseInt(year2) - Math.ceil(absM / 12 == 0 ? 1 : parseInt(absM) / 12)
    month2 = 12 - (absM % 12)
  }
  var day2 = day
  var days2 = new Date(year2, month2, 0)
  days2 = days2.getDate()
  if (day2 > days2) {
    day2 = days2
  }
  if (month2 < 10) {
    month2 = '0' + month2
  }

  // var t2 = year2 + '-' + month2 + '-' + day2;
  var t2 = year2 + '-' + month2
  return t2
}

/**
 * @description: ()
 * @param {*} dateInitial
 * @param {*} dateFinal
 * @return {*}
 */
export const getDaysDiffBetweenDates = (dateInitial, dateFinal) => (dateFinal - dateInitial) / (1000 * 3600 * 24)
